.Article{
    width:100%;
}
#title-article, #section, #date, #imgTitle, p{
    position: relative;
    left:20%;
    width: 60%;
}
#title-article{
    font-size: 4vw;
    text-align: left;
    margin-top: 12vw;
}
#section{
    margin-top: 1vw;
    margin-bottom: 1vw;
    font-size: 1.3vw;
    color: #444;
}
#date{
    font-size: 1.1vw;
    font-family: 'Lato';
    margin-top: 1vw;
    margin-bottom: 1vw;
    color: #777;
    font-weight:300; 
}
#imgTitle{
    margin-top: 1vw;
    margin-bottom: 2vw;
}
.Article p{
    margin-top: 1vw;
    margin-bottom: 1vw;
    text-align: left;
    color: #313131;
    font-family:  'Merriweather', serif;
    font-size: 1.6vw;
    line-height: 2.5vw;
}
.Article strong{
    font-family:  'Merriweather', serif;
    color: #313131;
    font-size: 1.6vw;
}