.Logo{
    position: relative;
    height: auto;
    display: flex;
    flex-direction: row;
 }
.line{
    width: 13vw;
    height: 0.57vw;
    border-radius: 0.295vw;
    background-color: #f9c34c;
    transform: rotate(50deg);
    position: relative;
    left: 7vw;
    top: 4.2vw;
}
.facts{
    font-size: 2vw;
    line-height: 2.7vw;
    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}
.span1{
    margin-left: 2vw;
}
.span2{
    margin-left:5vw;
}

@media (max-width: 700px) {
    .line{
        width: 22vw;
        height: 0.57vw;
        position: relative;
        left: 9vw;
        top:8vw; 
        transform: rotate(60deg);
    }
    .facts{
        font-size: 5vw;
        line-height: 5.5vw;
    }
}