.Form{
    position: relative;
    width:100%;
    margin-top: 3%;
}
.Form form{
    width: 35vw;
    margin: 0 auto;
    padding: 1em;
}
.Name, .Email{
    width: 100%;
    position: relative;
    margin-bottom: 0.6vw;
    height: 2.5vw;
    font-family: 'Lato';
}
.Name{
    width: 100%;
}

.Send{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
}
.Send textarea{
    width: 88%;
    height: 6vw;
    max-height: 6vw;
    resize: none;
    /* vertical-align: top; */
    font-family: 'Lato';
}
.Send input{
    width: 10%;
    height: 6vw;
    border: none;
    background: #444;
    color: #bbb;
    font-size: 2vw;
    margin-left: 2%;
}
.Send input:hover{
    background: #888;
    color: #444;
}