.profile{
    position: relative;
    width: 40%;
    height: 100%;
    background: #fff;
    background-image: url('./wolf.png');
    background-attachment: fixed;
    background-position-x: 2vw;
    background-position-y: 30%;
    background-repeat: no-repeat;
    background-size: 7vw;
}
.profile img{
    position: absolute;
    width: 50%;
    left: 25%;
    bottom: 50%;
}
.profile h1{
    position: absolute;
    bottom:42%;
    left:33%;
    font-size: 2.2vw;
    text-align: center;
}

.profile p{
    font-family: 'Lato';
    font-weight: 100;
    font-size: 1.5vw;
    text-align: center;
    width: 70%;
    position: absolute;
    bottom: 30%;
    left: 15%;
}
.profile p span{
    font-family: 'Lato';
    font-weight: 100;
    color: #f9c34c;
}
.contact-profile{
    position: absolute;
    bottom: 30%;
    left: 30%;
}

@media (max-width: 700px){
    .profile{
        width: 100%;
        background-position-x: 5%;
        background-position-y: 10%;
        background-size: 15%;
    }
    .profile img{
        width:40%;
        left: 30%;
        bottom:60%;
    }
    .profile h1{
        bottom:45%;
        width:40%;
        left:30%;
        font-size: 6vw;
    }
    .profile p{
        font-size: 4.5vw;
        bottom: 25%;
    }
    .contact-profile{
        bottom: 25%;
        left: 25%;
    }
}