.Home{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #fff;
}
.Title{
    position: absolute;
    width: auto;
    letter-spacing: 8px;
    left: 20vw;
    bottom: 65vh;
    color: #111;
}
.Title h1{
    position: relative;
    font-size: 5vw;
    text-align: right;
    line-height: 0.9;
    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
}

.Title h4{
    font-family: 'Lato';
    font-size: 1.7vw;
    font-weight: 100;
    position: absolute;
    top: 25%;
    right: -4vw;
    transform: rotate(270deg);
}
.Logo-Home{
    position: absolute;
    bottom: 40vh;
    left: 10vw;
    -webkit-animation: slide-fwd-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.3s both;
	        animation: slide-fwd-center 0.7s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.3s both;
}
.Btn-Contact{
    position: absolute;
    bottom: 40vh;
    left:30vw;
}
.Dev-img img{
    position: absolute;
    width: 25vw;
    bottom: -5vw;
    right: 10vw;
}
.Down{
    position: absolute;
    width: 4.6vw;
    bottom: 2vw;
    left: 47vw;
}
.Down>a>img{
    width: 4.6vw;
}

/* keyframes */
@-webkit-keyframes slide-fwd-center {
    0% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
    }
    100% {
      -webkit-transform: translateZ(160px);
              transform: translateZ(160px);
    }
  }
  @keyframes slide-fwd-center {
    0% {
      -webkit-transform: translateZ(0);
              transform: translateZ(0);
    }
    100% {
      -webkit-transform: translateZ(160px);
              transform: translateZ(160px);
    }
  }
  
  /*title keyframe*/
  @-webkit-keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }

  /* mobile */
  @media (max-width: 700px) {
    .Title{
      bottom: 70vh;
      left: 5%;
    }
    .Title h1{
      font-size: 12vw;
    }
    .Title h4{
      font-size: 4vw;
      top: 35%;
      right: -8vw;
    }
    .Logo-Home{
      position: absolute;
      bottom: 50vh;
      left: 0vw;
    }
    .Btn-Contact{
      left:10vw;
    }
    .Dev-img img{
      width: 80vw;
      bottom: -15vw;
      right: -30vw;
    }
    .Down{
      width: 10vw;
      bottom: 6vw;
      left: 40vw;
  }
    .Down a img{
        width: 15vw;
    }
}