.ProjectsCarousel{
  overflow-x: hidden;
}
.ProjectsCarousel h1{
    width: 30%;
    position: relative;
    font-size: 2.6vw;
    letter-spacing: 4px;
    left: 35%;
    text-align: center;
  }
  .ProjectsCarousel img{
    width: 45%;
    position: relative;
    left: 24.5%;
  }
  /* arrow buttons */
.right-btn, .left-btn{
  position: absolute;
  top:40%;
  cursor: pointer;
  z-index: 10;
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  transition: background 0.3s ease-in-out;
}
.right-btn{
  right: 7%;
}
.right-btn:hover, .left-btn:hover{
  background: rgba(0,0,0,0.2);
}
.left-btn{
  left: 7%;
}
.right-btn img{
  width:10vw;
  position:relative;
  left: 50%;
  top:50%;
  transform: translateX(-50%) translateY(-50%);
}

.left-btn img{
  position: relative;
  width:10vw;
  left: 50%;
  top:50%;
  transform: translateX(-50%) translateY(-50%) scaleX(-1);;
}
