
/* skill-component */
.skill-component{
    position: relative;
    bottom:60%;
    left: 10%;
    width: 45vw;
    display: flex;
    flex-direction: row;
    margin: 0.7vw;
  }
  .skill-component h1{
    margin-right: 2vw;
    font-size: 2vw;
  }
  .skill-loader{
    width: 90%;
    position: relative;
    right: 0;
    transition: padding 0.3s;
  }
  .skill-loader div{
    background: #333;
    width: 100%;
    height: 0.7vw;
    position: absolute;
    top: 45%;
    border-radius: 3.5vw;
  }
  .skill-loader div:nth-child(2){
    background: #f9c34c;    
  }
  .skill-loader:hover{
    padding: 0.1vw;
  }
  /* Transition of skill yellow */
  .skillValue{
    width: 3vw;
    transition: width ease-in-out;
  }

  @media(max-width:700px){
    .skill-component{
      left: 5%;
      width: 90vw;
      display: flex;
      flex-direction: row;
      margin: 2vw;
    }
    .skill-component h1{
      font-size: 5vw;
    }
    .skill-loader div{
      background: #333;
      height: 1.7vw;
    }
  }