.ProjectsView{
    height: 50%;
    width: 70%;
    position: fixed;
    top: 8%;
    left: 15%;
    background-color: #ffffff;
    z-index: 500;
    display: flex;
    flex-direction: column;
}
.ProjectImage{
    background: #ccc;
    position: relative;
    height: 60vh;
    width: 100%;
    background-repeat: none;
    background-size: 100%;
}
.ProjectImage img{
    width: 100%;
    position: relative;
    left:0;
}
@media (max-width: 600px){
    .ProjectsView{
        height: 90vh;
        width: 95%;
        margin: 2.5%;
    }
}
/* Arrows */
#left, #right{
    background: rgba(0, 0, 0, 0.35);
    width: 2.5vw;
    height: 2.5vw;
    border-radius: 50%;
    position: absolute;
    cursor: pointer;
    transition: padding 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275)
}

#left img, #right img{
    position: relative;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}
#left img{
    transform:  translateX(-50%) translateY(-50%) rotate(180deg);
}
#left:hover, #right:hover{
    padding: 0.05vw;
    background: rgba(0, 0, 0, 0.5);
}
#left{
    left:1vw;
    top:48%;
}
#right{
    top:48%;
    right: 1vw;
}
/* close btn */
#close-view{
    position: absolute;
    color: #111;
    left: 1vw;
    top: 1vw;
    font-size: 2vw;
    z-index: 100;
    cursor: pointer;
    transition: transform 0.2s;
}
#close-view:hover{
    transform: rotate(90deg);
}
/* Description styles */
.ProjectDesc{
    z-index: 100;
    background: #fff;
    height: 40vh;
    border-bottom-left-radius: 0.5vw;
    border-bottom-right-radius: 0.5vw;
}
.ProjectDesc h1{
    width: 100%;
    position: relative;
    text-align: left;
    left: 0;
    margin: 1.6vw;
}
.ProjectDesc p{
    margin: 1.6vw;
    font-family: 'Lato';
    font-size: 1.1vw;
    margin-bottom: 0;
}
/* Skills used */
.skillsUseProject{
    /* ubicar las skills lateralmente */
    display: flex;
    flex-direction: row;
}