.SkillUse{
    position: relative;
    width: max-content;
    margin: 1vw;
}
.SkillUse h5{
    color: #111;
    font-size: 0.8vw;
    width: max-content;
    text-align: center;
    position: relative;
    padding: 0.7vw 1vw;
    border-radius: 1vw;
    background: rgb(165, 165, 160);
    cursor: pointer;
    transition: background-color 0.9s cubic-bezier(0.175, 0.885, 0.32, 1.275)
}
.SkillUse h5:hover{
    background: rgba(249, 194, 76, 0.884);
}