#list-blog{
    width: 60%;
    height: 90%;
    top: 3vh;
    position: relative;
    display:flex;
    flex-direction: column;
    overflow-y: scroll;
}
#list-blog h2{
    position: relative;
    width: 20%;
    font-family: 'Lato';
    font-size: 1.4vw;
    left: 2vw;
}
/* Article View */
#ArticleView{
    width: 95%;
    left: 1vw;
    height: 10vw;
    margin-top: 1vw;
    position: relative;
    background: #fff;
    padding: 0.5vw;
    border: 1px solid #fff;
    border-radius: 10px;
    transition: all 0.5s ease-in-out;
    color: #111;
}
#ArticleView:hover{
    border: 1px solid rgba(85, 85, 85, 0.281);
}
#ArticleView img{
    position: relative;
    width: 25%;
}
#ArticleView h4{
    position: absolute;
    top: 0%;
    left: 27%;
    font-family: 'Lato';
    font-weight: 100;
    font-size: 1.2vw;
}
#ArticleView h1{
    position: absolute;
    width: 70%;
    top: 13%;
    left: 27%;
    font-size: 1.8vw;
}
#ArticleView p{
    position: absolute;
    margin: 0;
    top: 60%;
    left: 27%;
    font-family: 'Merriweather';
    font-size: 1.2vw;
}