.Footer{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #fff;
    display: flex;
    flex-direction: column;
}
.Contact-form{
    width: 100%;
    height: 50%;
}

/* footer */
.foot{
    background: #222;
    width: 100%;
    height: 50%;
    position: relative;
    bottom: 0;
}
.up-btn{
    background: rgb(249, 195, 76);
    position: absolute;
    width: 8vw;    
    height: 8vw;
    left: 46vw;
    top:-4vw;
    border-radius: 50%;
}
.up-btn:hover{
    background: rgba(249, 195, 76);
}
.up-btn a{
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.up-btn a img{
    width: 3vw;
    position: relative;
    left: 50%;
    top:50%;
    transform: translateX(-50%) translateY(-50%);
    /* transform: rotate(-90deg); */
}
.foot h3{
    position: absolute;
    bottom: 2%;
    left: 35%;
    width: 30%;
    text-align: center;
    font-weight: 400;
    font-size: 1vw;
    color: #666;
    font-family: 'Lato';
}
.foot span{
    font-family: 'Lato';
    color: rgb(249, 195, 76);
}

/* social */
.social {
    position: absolute;
    width: 27%;
    left: 37%;
    top: 20%;
    display: flex;
    flex-direction: row;
}
.social div{
    position: relative;
    width: 5vw;
    height: 5vw;
    background: rgb(249, 195, 76);
    margin: 2vw;
    border-radius: .5vw;
}
.social div a{
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: .5vw;
}
.social div img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 70%;
}