.Projects{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #fff;
    display: flex;
    flex-direction: column;
}
.Projects-title{
    height: 20%;
}
.Projects-title div{
    position: absolute;
    width: 40%;
    height: 0.6vw;
    left: 30%;
    top: 10vh;
    background: #f9c34c;
    border-radius: 0.3vw;
}
.Projects-title h1{
    font-size: 5vw;
    width: 70%;
    position: absolute;
    top: 5vh;
    letter-spacing: 5px;
    text-align: center;
    left:15%;
}

.Projects-ex{
    height: 80%;
}

