.Skills{
    position: relative;
    width: 100%;
    height: 100vh;
    background: #fff;
    display: flex;
    flex-direction: row;
}

/* Skills div */
.skills-div{
    position: relative;
    width: 60%;
    height: 100%;
}
.Rank-div{
    /* editar acÃ¡ para mover la lista de skill */
    position: absolute;
    bottom: 40%;
}

/* mobile */
@media (max-width: 700px){
    .Skills{
    flex-direction: column;
    }
    .skills-div{
        position: relative;
        width: 100%;
        height: 100%;
    }
    .Rank-div{
        position: absolute;
        width:100%;
        bottom: 25%;
    }
}
