.ProjectsComponent{
    position: relative;
}
.View-btn{
    position: absolute;
    left: 30%;
    bottom: 0;
    cursor: pointer;
}
.View-btn a{
    text-decoration: none;
    color: #111;
    font-weight: 100;
    font-size: 1.6vw;
    font-family: 'Lato';
    padding: 0.4vw 6vw;   
    text-align: center;
    border: 1px solid #333;
    text-decoration: none;
}
.View-btn a:hover{
    background: rgba(0,0,0,.1)
}