.Card{
    position: relative;
    width: 19vw;
    height: 24vw;
    background-color: #f9c34c;
    border-radius: 0.7vw;
}
.Card h1{
    position: relative;
    width: 50%;
    font-size: 2.2vw;
    text-align: center;
    top: 12%;
    left: 24%;
}
.Card h4{
    position: relative;
    width: 50%;
    font-family: 'Lato';
    font-weight: 500;
    font-size: 1.5vw;
    text-align: center;
    top: 15%;
    left: 24%;
}

@media (max-width: 700px){
    .Card{
        width: 55vw;
        height: 65vw;
        margin: 2vw;
        transition: all ease-in-out 0.3s;
    }
    .Card:hover{
        width: 60vw;
        height: 70vw;
    }
    .Card h1{
        font-size: 6vw;
    }
    .Card h4{
        font-size: 4vw;
    }
}