.Menu{
    position: fixed;
    display: block;
    width: 100%;
    height: 100vh;
    z-index: 1000;
    background: #fff;
    margin-left: -100%;
    opacity: 0;
    
   /*   -webkit-animation: slide-right 0.8s cubic-bezier(0.860, 0.000, 0.070, 1.000) both;
     animation: slide-right 0.8s cubic-bezier(0.860, 0.000, 0.070, 1.000) both; */

     transition: all .4s ease-in-out;
}
.menu-icon img{
  width: 2.5vw;
}
.Menu-open{
  margin-left: 0px;
  opacity: 1;
}

.Menu img{
    position: absolute;
    width: 25vw;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 900;
}
.Menu ul{
    position: relative;
    top: 40%;
    transform: translateY(-50%);
    list-style: none;
}
.Menu ul li{
    margin-top: 5vw;
}
.Menu ul li a{
    color: #111;
    text-decoration: none;
    position: relative;
    transition: font-size 0.1s ease-in;
    font-size: 3vw;
    left: 5vw;
}
.Menu ul li a:hover{
  font-size: 3.1vw;
}


/* Keyframes */
@-webkit-keyframes slide-right {
    0% {
      -webkit-transform: translateX(-80%);
              transform: translateX(-80%);
    }
    100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }
  @keyframes slide-right {
    0% {
      -webkit-transform: translateX(-80%);
              transform: translateX(-80%);
    }
    100% {
      -webkit-transform: translateX(0%);
              transform: translateX(0%);
    }
  }

 
@media (max-width: 700px){
  .menu-icon{
    position: fixed;
    left: 8vw;
    top:8vw;
  }
  .menu-icon img{
    width: 8vw;
  }
}