#recent-blog{
    width: 35%;
    height: 97%;
    position: relative;
    margin-left: 5%;
    position: relative;
    border: 1px solid #fff;
    border-radius: 10px;
    transition: all 0.5s ease-in-out;
    
}
#recent-blog:hover{
    border: 1px solid rgba(85, 85, 85, 0.281);
}
#recent-blog h4{
    position: relative;
    left:5%;
    top: 4vh;
    font-family: 'Lato';
    font-size: 1.4vw;
    font-weight: 100;
}
#recent-blog img{
    position: relative;
    left: 5%;
    top: 5vh;
    width: 90%;
}
#recent-blog h1{
    position: relative;
    left:5%;
    margin-top: 3vw;
    width: 90%;
    font-size: 1.8vw;
}
#recent-blog p{
    position: relative;
    left:5%;
    width: 90%;
    margin-top: 1vw;
    font-family:  'Merriweather', serif; 
    font-size: 1.4vw;
    font-weight: 100;
}
#readMore{
    position: relative;
    left: 5%;
    margin-top: 2vw;
    width: 90%;
}
#readMore a{
    position: absolute;
    right: 0;
    color: #111;
    font-family: 'Lato';
    text-decoration: none;
    border: 1px solid #111;
    padding: 1vw 4vw;
    transition: all 0.2s ease-in;
} 
#readMore a:hover{
    color: #414141;
    border-color: #414141;
}